@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap);
*{
    font-family: 'Poppins';
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}


nav{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

nav .logo{
    position: relative;
    max-width: 80px;
}

nav ul{
    position: relative;
    display: -webkit-flex;
    display: flex;
}

nav ul li{
    list-style: none;
}

nav ul li a{
    display: inline-block;
    color: #333;
    font-weight: 400;
    margin-left: 40px;
    text-decoration: none;
}

nav ul li a:hover{
    color: #08975c;
    /* transition: .5s all ease-in-out; */
}

@media only screen and (max-width: 768px){
    nav{
        padding: 1rem 1rem;
    }
    nav .logo{
        max-width: 50px;
    }
    nav ul{
        display: none;
    }
}
.circle{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #017143;
    -webkit-clip-path: circle(600px at right 800px);
            clip-path: circle(600px at right 800px);
}

.content{
    position: relative;
    width: 90%;
    margin: 100px auto 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    /* background-color: #333; */
}

.content .textbox{
    position: relative;
    max-width: 600px;
}

.content .textbox h2{
    color: #333;
    font-size: 4em;
    line-height: 1.43em;
    font-weight: 500;
}

.content .textbox h2 span{
    color: #017143;
    font-size: 1.2em;
    font-weight: 900;
}

.content .textbox p{
    color: #333;
}

.content .textbox a{
    display: inline-block;
    margin-top: 20px;
    padding: 8px 20px;
    background: #017143;
    color: #fff;
    border-radius: 40px;
    font-weight: 500;
    letter-spacing: 1px;
    text-decoration: none;
}

.content .imgBox{
    width: 600px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    padding-right: 50px;
    margin-top: 50px;
}

.content .imgBox img{
    max-width: 350px;
}

.thumb{
    position: absolute;
    left: 50%;
    bottom: 20px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: -webkit-flex;
    display: flex;
}

.thumb li{
    list-style: none;
    display: inline-block;
    margin: 0 20px;
    cursor: pointer;
}

.thumb li:hover{
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}

.thumb li img{
    max-width: 60px;
}

@media only screen and (max-width: 768px){
    .content{
        width: 90%;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        grid-gap: 3rem;
        gap: 3rem;
    }
    .content .textbox{
        position: relative;
        max-width: 100%;
        /* border: 1px solid black; */
    }
    .content .textbox h2{
        color: #333;
        font-size: 2.2em;
        line-height: 1.5em;
    }
    .content .imgBox{
        width: 600px;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        padding: 0;
        margin: 0;
    }
    .circle{
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(0,50%);
                transform: translate(0,50%);
        background-color: #017143;
        -webkit-clip-path: circle(500px at center bottom);
                clip-path: circle(500px at center bottom);
    }
    .thumb{
        position: absolute;
        left: 50%;
        bottom: -50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        display: -webkit-flex;
        display: flex;
    }
}
